body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
p {
  font-size: 3rem;
}
h1 {
  font-size: 6rem;
}
.App {
  width: 100vw;
  height: 100vh;
  margin: auto;
  display: flex;
  position: relative;
  background-color: black;
  color: white;
  background: url("./assets/bright-pop-landscape-design.jpg") no-repeat center
    center/cover;
}
input {
  height: 5vh;
  background: transparent 2px;
  width: 40vw;
  color: white;
  margin-top: 30px;
  margin-left: 10vw;
  text-align: center;
  font-size: large;
  font-weight: 600;
  border-radius: 10px;
  background-color: bisque;
  background: rgba(255, 255, 255, 0.26);
}
.btn {
  width: 20vw;
  /* margin: 20px 40px 30px; */
  margin-left: 30px;
  height: 5vh;
  border: black 2px solid;
  border-radius: 30px;
  text-align: center;
  font-weight: 700;
  background: transparent;
  filter: blur(0px);
  color: #ffffff;
  font-size: large;
  /* padding: 13px 20px 10px 20px; */
  text-decoration: none;
}

.btn:hover {
  background: #4040fb;
  text-decoration: none;
}
.top {
  width: 100%;
  position: relative;
  top: 10vh;
  left: 10vw;
}
h4 {
  font-size: 8vh;
  font-weight: 800;
}
.bottom {
  position: relative;
  left: 5vw;
  display: flex;
  justify-content: space-around;
  top: 35vh;
}
.bold {
  font-size: 25px;
  font-weight: 700;
}
h3 {
  padding-top: 20px;
  font-family: Arial, Helvetica, sans-serif;
}
@media (max-width: 480px) {
  input {
    width: 70vw;
    font-size: medium;
  }
  body {
    background-color: black;
  }
  .btn {
    background-color: #4040fb;
  }
  .App {
    background-color: black;
    background-image: none;
  }
  .btn {
    font-size: medium;
    width: 34vw;
    margin-top: 20px;
  }
  .top {
    padding: 10px;
    position: relative;
    left: 10px;
  }
  .bottom {
    padding: 20px;
    position: relative;
    left: 6px;
    display: flex;
    flex-direction: column;
  }
}
